
#HeaderBar{
		width: 100%;
		height: 70px;
		background-color: white;
		position: fixed;
}
.headerImg{
		width: 32px;
		height: 32px;
		position: relative;
		top: 15px;
		left: 93%;
		cursor: pointer;
}
#textUl li{
		position: relative;
		display: inline;
		top: -17px;
		font-size: 23px;
		font-weight: 700;
		font-family: "微软雅黑";
}
#textUl li a{
		text-decoration:none;
		color: #515151;
}
#textUl li a:hover{
		color: grey;
}
#homeText{
		left: 5%;
}
#showText{
		left: 9%;
}
#loginText{
		left: 75%;
}
#SideBar{
		position: fixed;
		right: 0px;
		top: -80px;
		width: 300px;
		height: 113%;
		background-color: #515151;
		z-index: 10;
}
#closeSide{
		position: absolute;
		cursor: pointer;
		width: 30px;
		height: 30px;
		top: 100px;
		left: 25px;
}




