
#dataDiv{
		position: absolute;
		top: 10%;
		left: 37.2%;
		width: 57%;
		height: 80%;
}
#dataDiv div{
		position: relative;
}
#gender{
		top: 5%;
}
#gender img{
		width: 28px;
		position: relative;
		top: 5px;
}
#sage{
		top:15%;
}
#sname{
		top:25%;
}
#university{
		top:35%;
}
#pro{
		top:45%;
}
#dir{
		top:55%;
}
#dataDiv span{
		position: relative;
		font-family: "微软雅黑";
		font-size: 20px;
		font-weight: 600;
		color: #515151;
}
#dataDiv p{
		position: relative;
		font-family: "微软雅黑";
		font-size: 22px;
		font-weight: 580;
		color: gray;
		display: inline;
}
#dataLine{
		position: absolute;
		top: 60%;
		left: 9.8%;
		width: 80px;
		height: 3px;
		background-color: #515151;
}
.fadeInf-enter-active, .fade-leave-active{
        -webkit-transition: 0.6s all ease;
        transition: 0.6s all ease;
}
.fadeInf-enter-active{
        opacity:1;
}
.fadeInf-leave-active{
        opacity:0;
}
.fadeInf-enter{
        opacity:0;
}



